import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import { SwipeCell, Swipe, SwipeItem, Lazyload, Tab, Tabs, Uploader, Dialog, Overlay, Search, Icon, Sticky, Tabbar, TabbarItem, Field, Button, Toast } from 'vant'
import VueI18n from 'vue-i18n'
import axios from 'axios'
import './assets/styles/reset.css'
import './assets/styles/animate.css'
// import fontSize from './assets/js/font-size';

Vue.use(VueI18n);
Vue.use(SwipeCell).use(Swipe).use(SwipeItem).use(Lazyload).use(Tab).use(Tabs).use(Uploader).use(Dialog).use(Overlay).use(Search).use(Icon).use(Sticky).use(Tabbar).use(TabbarItem).use(Field).use(Button).use(Toast);
// fontSize(document, window);

Vue.config.productionTip = false
axios.defaults.baseURL = process.env.NODE_ENV === 'development' ? '/api/mobile' : window.origin+'/mobile'
Vue.prototype.$axios = axios
const i18n = new VueI18n({
  locale: window.localStorage.getItem('lang') || 'zh-CN',    // 语言标识
  //this.$i18n.locale // 通过切换locale的值来实现语言切换
  messages: {
    'zh-CN': require('./assets/js/lang/zh'),   // 中文语言包
    'en-US': require('./assets/js/lang/en')    // 英文语言包
  }
});
router.beforeEach((to,from,next)=>{
  if(to.path=="/"){
    window.localStorage.setItem("keyIndex", JSON.stringify(0))
  }else if(to.path=="/message"){
    window.localStorage.setItem("keyIndex", JSON.stringify(1))
  }else if(to.path=="/discover"){
    window.localStorage.setItem("keyIndex", JSON.stringify(2))
  }
  next()
})
new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
