import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    lang: window.localStorage.getItem('lang') || 'zh-CN',
    userInfoObj:JSON.parse(window.localStorage.getItem('userInfoObj')) || {},
    moduleidList: JSON.parse(window.localStorage.getItem('moduleidList')) || [],
    tabbarFlag:0
  },
  mutations: {
    changeLang(state, lang) {
      state.lang = lang;
      window.localStorage.setItem('lang', lang);
    },
    userInfo(state,userInfoObj){
      state.userInfoObj = userInfoObj
      window.localStorage.setItem('userInfoObj',JSON.stringify(userInfoObj))
    },
    setModuleidList(state, moduleidList) {
      state.moduleidList = moduleidList;
      window.localStorage.setItem('moduleidList',JSON.stringify(moduleidList))
    },
    tabbarFlagfn(state,tabbarFlag){
      state.tabbarFlag = tabbarFlag
    }
  },
  actions: {
  },
  modules: {
  }
})
