import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  //首页
  {
    path: '/',
    name: 'Home',
    component: ()=>import('../views/Home/Home.vue'),
  },
  //首页-掌上播报页
  {
    path:'/broadcast',
    name:'Broadcast',
    component:()=>import('../views/Home/Broadcast.vue'),
  },
  //首页-业务展示详情
  {
    path:'/recommenddetail',
    name:'RecommendDetail',
    component:()=>import('../views/Home/RecommendDetail.vue'),
  },
  //首页-我的足迹
  {
    path: '/myFootprint',
    name: 'MyFootprint',
    component: ()=>import('../views/Home/MyFootprint.vue'),
  },
   //发现页
   {
    path: '/discover',
    name: 'Discover',
    component: ()=>import('../views/Discover/Discover.vue'),
  },
   //我的
   {
    path: '/mine',
    name: 'Mine',
    component: ()=>import('../views/Mine/Mine.vue'),
  },
  // 跳转第三方
  {
    path: '/outerIframe',
    name: 'OuterIframe',
    component: () => import( /* webpackChunkName: "OuterIframe" */ '../views/OuterIframe.vue')
  },
  // 搜索页
  {
    path: '/search',
    name: 'Search',
    component: ()=>import('../views/Search/Search.vue'),
  },
  // 搜索结果页
  {
    path: '/search-result',
    name: 'SearchResult',
    component: ()=>import('../views/Search/Search-result.vue'),
  },
  // 我的关注
  {
    path: '/myConcerns',
    name: 'MyConcerns',
    component: ()=>import('../views/MyConcerns/MyConcerns.vue'),
  },
  // 我的关注-搜索
  {
    path: '/myConcerns-search',
    name: 'MyConcernsSearch',
    component: ()=>import('../views/MyConcerns/MyConcerns-search.vue'),
  },
  // 消息列表
  {
    path: '/message',
    name: 'Message',
    component: ()=>import('../views/Message/message.vue'),
  },
  // 消息-聊天
  {
    path: '/message-chat',
    name: 'MessageChat',
    component: ()=>import('../views/Message/message-chat.vue'),
  },
  // 消息-搜索
  {
    path: '/message-search',
    name: 'MessageSearch',
    component: ()=>import('../views/Message/message-search.vue'),
  },
  //404error
  {
    path:'/error',
    component:()=>import('../components/error404.vue')
  }
]
const router = new VueRouter({
  routes
})

export default router